import axios from 'axios';
import { LOCAL_API_BASE_URL } from '../global_variables'
import qs from 'qs';

/**
 * 서버에 데이터를 전송하는 모듈
 * @param {*} mappingUrl -- URL
 * @param {*} reqType -- Request Type
 * @param {*} reqData -- Request Data
 * @returns response
 */
async function AxiosApi(mappingUrl, reqType, reqData) {
    let resData = [];
    axios.defaults.baseURL = LOCAL_API_BASE_URL;
    // axios.defaults.withCredentials = true;

    console.log(reqData);

    await axios({
            method: reqType,
            url: mappingUrl + (
                reqType === 'get'
                    ? '?' + qs.stringify(reqData)
                    : ''
                ),
            responseType: 'json',
            data: reqData,
        })
        .then(function (response) {
            console.log(response);
            resData.data = response.data;

            if (resData.data)
                resData.stts = true;
            else
                resData.stts = false;
        })
        .catch(function(error) {
            console.log(error.code);
            resData = {
                stts : false,
                data : error.code
            };
        });
    
    return ( resData );

} export default AxiosApi;