import React, { useRef } from "react";
import { Layout, Row, Col, message, Form, Input } from "antd";
import "../App.css";
import "../css/login.css";
import AxiosApi from "../util/AxiosApi.js";

const Login = ({ isLogin, signupPage, openNotification }) => {
  const loginRef = useRef([]); // 입력한 id, pw
  const [msg, setMsg] = message.useMessage();

  // 로그인
  const onLogin = async (option) => {
    const resultData = await AxiosApi("checkMember", "get", option);

    if (!resultData.stts) {
      if (resultData.data === "ERR_NETWORK") {
        openNotification();
      } else {
        msg.open({
          type: "error",
          content: "아이디, 패스워드를 확인해주세요.",
          duration: 2,
        });
      }
    } else {
      isLogin();
    }
  };

  // 회원가입
  const onSignUp = () => {
    signupPage();
  };

  return (
    <Layout className="layout">
      <Row className="login-box">
        <Col className="img-area">
          <h1 className="title">iPhone 15</h1>
          <div>
            <p className="sub-title">2023-09-22(금) 출시 예정</p>
            <p className="sub-title">USB-C 포트 탑재</p>
            <p className="sub-title">다이나믹 아일랜드 적용</p>
            <p className="sub-title">
              사라진 음소거 버튼과 새로 생긴 액션 버튼
            </p>
            <p className="sub-title">신규 색상 추가</p>
          </div>
        </Col>
        <Col className="input-area-login">
          <Form className="login-form" name="loginForm" onFinish={onLogin}>
            <h1 className="title">Template</h1>
            {/* <label className="form-label">아이디</label> */}
            <Form.Item
              name="id"
              rules={[
                {
                  required: true,
                  message: "아이디를 입력해주세요.",
                },
              ]}
            >
              <Input
                className="form-input"
                placeholder="아이디"
                ref={(el) => (loginRef.current[0] = el)}
              />
            </Form.Item>
            {/* <label className="form-label">비밀번호</label> */}
            <Form.Item
              className="form-item"
              name="pw"
              rules={[
                {
                  required: true,
                  message: "비밀번호를 입력해주세요.",
                },
              ]}
            >
              <Input
                type="password"
                className="form-input"
                placeholder="패스워드"
                ref={(el) => (loginRef.current[1] = el)}
              />
            </Form.Item>
            <Form.Item>
              <button type="submit" className="btn-fade btn-primary btn-full">
                로그인
              </button>
              <a className="link link-primary link-full" onClick={onSignUp}>회원가입</a>
            </Form.Item>
          </Form>
        </Col>
      </Row>
      {setMsg}
    </Layout>
  );
};
export { Login };
