import React, { useState } from "react";
import { Col, Row, Button, Menu } from "antd";
import "../App.css";
import CommHeaderData from "./CommHeaderData.js";

const items = CommHeaderData.MenuList;

const CommHeader = ({ cert, mainPage, loginPage, isLogout, introPage, boardPage }) => {
  const [current, setCurrent] = useState("");

  const loadMain = () => {
    setCurrent("");
    mainPage();
  };

  const onClick = (e) => {
    console.log("click ", e);
    setCurrent(e.key);

    if (e.key === "introduction") introPage();
    else if (e.key === "notice") boardPage("공지사항");
  };

  return (
    <Row className="nav-alignRight">
      <Col span={3} className="title">
        <a onClick={loadMain}>Template</a>
      </Col>
      <Col span={18}>
        <Menu
          className="menu"
          onClick={onClick}
          selectedKeys={[current]}
          mode="horizontal"
          items={items}
        />
      </Col>
      <Col span={3} className="align-items-center">
        <button type="primary" className="btn-fade btn-primary" onClick={cert ? isLogout : loginPage}>
          로그{cert ? "아웃" : "인"}
        </button>
      </Col>
    </Row>
  );
};
export { CommHeader };
