import React  from "react";
import { Row, Col, Table, Input } from "antd";
import { LoadingOutlined } from '@ant-design/icons';
import '../../css/content.css';
import AxiosApi from "../../util/AxiosApi.js";

const columns = [
    {
        title: '번호',
        dataIndex: 'postSeq',
        key: 'postSeq',
        align: 'center',
        width: '5%',
    },
    {
        title: '제목',
        dataIndex: 'postTitle',
        key: 'postTitle',
        align: 'center',
        width: '50%',
    },
    {
        title: '작성자',
        dataIndex: 'postRegMem',
        key: 'postRegMem',
        align: 'center',
        width: '15%',
    },
    {
        title: '작성일',
        dataIndex: 'postRegDt',
        key: 'postRegDt',
        align: 'center',
        width: '20%',
    },
    {
        title: '조회수',
        dataIndex: 'postViews',
        key: 'postViews',
        align: 'center',
        width: '10%',
    },
];

const spinIcon = <LoadingOutlined className="primary-color" style={{ fontSize: 24 }} />;

const uploadImg = async (e) => {
    e.preventDefault();

    if (e.target.files) {
        let files = Array.from(e.target.files);
        let formData = new FormData();

        files.forEach(file => {
            formData.append('files', file);
        });

        AxiosApi('uploadFile', 'post', formData);
    }
}

const Notice = ({tableData}) => {
    return (
        <div className="contentBox">
            <Row className="row">
                <Col span={3} />
                <Col span={18} className="align-center">
                    <h1 className="content-title">
                        공지사항
                    </h1>
                </Col>
                <Col span={3} />
            </Row>
            <Row className="row">
                <Col span={3} />
                <Col span={18}>
                    {/* <Table className="table" columns={columns} dataSource={tableData} size="middle" loading={{ indicator: spinIcon, spinning: true }} /> */}
                    <Table className="table" columns={columns} dataSource={tableData} size="middle" />
                </Col>
                <Col span={3} />
            </Row>
            <Row className="row">
                <Col span={3} />
                <Col span={18}>
                    <Input type="file" id="profile-upload" accept="image/*" onChange={uploadImg} multiple />
                </Col>
                <Col span={3} />
            </Row>
        </div>
    );
};

export { Notice };
