import React, { useState, useEffect } from "react";
import { Layout, message, notification, Spin } from "antd";
import "../App.css";
import AxiosApi from "../util/AxiosApi";

import { CommHeader } from "../header/CommHeader.js";
import { Main } from "../content/Main.js";
import { Login } from "../content/Login.js";
import { Signup } from "../content/Signup.js";
import { Introduction } from "../content/Introduction";
import { Notice } from "../content/board/Notice";

const { Header, Content, Footer } = Layout;

const CommLayout = () => {
  
  // 인증
  const [cert, setCert] = useState(false);
  const isLogin = () => {
    setCert(true);
    popMsg('로그인 되었습니다.');
    loadContent("Main");
  };

  const isLogout = () => {
    setCert(false);
    popMsg('로그아웃 되었습니다.');
    loadContent("Main");
  };

  // 팝 메세지
  const [msg, setMsg] = message.useMessage();
  const popMsg = (content) => {
    msg.open({
      type: "success",
      content: content,
      duration: 2,
    });
  };

  // 팝 알림
  const [ntf, setNtf] = notification.useNotification();
  const openNotification = () => {
    ntf.info({
      message: "서버 연결이 원활하지 않습니다.",
      description: "잠시 후 다시 시도해주세요.",
      placement: "top",
      duration: 2.5,
      closeIcon: false,
      style: {
        width: "auto",
      },
    });
  };

  // 데이터
  const [tableData, setTableData] = useState([]);
  useEffect(() => {
    const result = AxiosApi("getBoardList", "get");
    setTableData(result.data);
  }, []);

  // 화면 전환
  const [content, setContent] = useState();
  useEffect(() => {
    setContent(<Main />);
  }, []);

  const loadContent = async (name) => {
    setContent(
      name === "Main" ? (
        <Main />
      ) : name === "Login" ? (
        <Login
          isLogin={isLogin}
          signupPage={signupPage}
          openNotification={openNotification}
        />
      ) : name === "Signup" ? (
        <Signup loginPage={loginPage} popMsg={popMsg} />
      ) : (
        <></>
      )
    );
  };

  const mainPage = () => {
    loadContent("Main");
  };
  const loginPage = () => {
    loadContent("Login");
  };
  const signupPage = () => {
    loadContent("Signup");
  };
  const introPage = () => {
    setContent(<Introduction />);
  };
  const boardPage = async (menu) => {
    const result = await AxiosApi("getBoardList", "get");
    setTableData(result.data);
    setContent(<Notice tableData={tableData} />);
  };

  return (
    <Layout className="layout">
      <Header className="header">
        <CommHeader
          cert={cert}
          mainPage={mainPage}
          loginPage={loginPage}
          isLogout={isLogout}
          introPage={introPage}
          boardPage={boardPage}
        />
      </Header>

      <Content className="content">
        {content}
      </Content>

      <Footer className="footer">Web-Template ©2023 Created by JISEONG</Footer>
      {setMsg}
      {setNtf}
    </Layout>
  );
};

export { CommLayout };