import React, { useRef, useState } from 'react';
import { Space, Layout, Row, Col, Input, Modal } from 'antd';
import '../App.css'
import '../css/signup.css';
import { UserModel } from '../data/UserData.js';
import AxiosApi from '../util/AxiosApi.js';
import { SuccessModal } from '../modal/SuccessModal.js';

const Signup = ({loginPage, popMsg}) => {
    const signupRef = useRef([]); // 입력한 id, pw
    const userModel = UserModel;
    const today = '2023-08-18';

    const [Modal, setModal] = useState(false);

    // 회원가입 체크
    const onSignup = async () => {
        let option = {};
        
        signupRef.current.forEach(item => {
            if (item.input.value === '') {
                alert(item.input.name + '를 입력해주세요.');
                let el = document.getElementById('valid' + item.input.name);
                return false;
            } else
                option[item.input.name] = item.input.value;
        });
        console.log(option);

        const resultData = await AxiosApi(('regMember'), 'post', option);
        if (resultData) {
            popMsg('회원가입 성공!');
            loginPage();
        }
    };

    const openModal = () => { setModal(true); };
    const closeModal = () => { setModal(false); loginPage(); };

    return (
        <Layout className='layout'>
            <Row className="signup-box">
                <Col className="input-area-signup">
                    <form id='FrmSignup' action='onSignup()'>
                        <div className="user-box">
                            <h1 className='title'>Sign Up</h1>
                        </div>
                        <Row>
                            <Col span={11}>
                                <div className="user-box">
                                    <Input type="text" name="id" id="id" required
                                        ref={el => signupRef.current[0] = el}/>
                                    <label>아이디</label>
                                    <div id="valid-id" hidden>아이디를 입력해주세요</div>
                                </div>
                            </Col>
                            <Col span={2}></Col>
                            <Col span={11}>
                                <div className="user-box">
                                    <Input type="text" name="name" id="name" required
                                        ref={el => signupRef.current[1] = el}/>
                                    <label>이름</label>
                                </div>
                            </Col>
                        </Row>
                        <Row>
                            <Col span={11}>
                                <div className="user-box">
                                    <Input type="password" name="pw" id="pw" required
                                        ref={el => signupRef.current[2] = el}/>
                                    <label>비밀번호</label>
                                </div>
                            </Col>
                            <Col span={2}></Col>
                            <Col span={11}>
                                <div className="user-box">
                                    <Input type="password" name="chPw" id="chPw" required
                                        ref={el => signupRef.current[3] = el}/>
                                    <label>비밀번호 확인</label>
                                </div>
                            </Col>
                        </Row>
                        <Row>
                            <Col span={11}>
                                <div className="user-box">
                                    <Input type="date" name="birth" id="birth" required defaultValue={today}
                                        ref={el => signupRef.current[4] = el}/>
                                    <label>생년월일</label>
                                </div>
                            </Col>
                            <Col span={2}></Col>
                            <Col span={11}>
                                <div className="user-box">
                                    <Input type="text" name="phone" id="phone" required
                                        ref={el => signupRef.current[5] = el}/>
                                    <label>전화번호</label>
                                </div>
                            </Col>
                        </Row>
                        <Row>
                            <Col span={24}>
                            <div className="user-box">
                                    <Input type="text" name="addr" id="addr" required
                                        ref={el => signupRef.current[6] = el}/>
                                    <label>주소</label>
                                </div>
                            </Col>
                        </Row>
                        <div className='signup-btn'>
                            <Space size={'middle'}>
                                <a onClick={onSignup}>
                                    Submit
                                </a>
                            </Space>
                        </div>
                    </form>
                </Col>
            </Row>
            {/* <SuccessModal closeModal={closeModal} /> */}
        </Layout>
    );
}; export { Signup };